import "./styles.css";
import styled from "@emotion/styled";
import { useEffect, useState, useCallback } from "react";

const height = 270;
const width = 180;

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;
const Animate = styled.div`
  background: url("./bg.png");
  background-position: ${(props) => props.x}px ${(props) => props.y}px;
  height: ${height}px;
  width: ${width}px;
  &:nth-child(1) {
    transform: scale(0.6);
    opacity: 0.5;
  }
  &:nth-child(2) {
    transform: scale(0.9);
    opacity: 0.7;
  }
  &:nth-child(3) {
    transform: scale(1);
    opacity: 0.8;
  }
  &:nth-child(4) {
    transform: scale(1.2);
    opacity: 0.9;
  }
  &:nth-child(5) {
    transform: scale(1.5);
    margin-left: 5em;
    margin-right: 1em;
  }
`;

const XCoords = [0, 180, -180];
const YCoords = [0, 270, -270];
const fps = 60;
const fpm = 30 * 1000;

export default function App() {
  const [x0, setX0] = useState(0);
  const [y0, setY0] = useState(0);
  const [x1, setX1] = useState(0);
  const [y1, setY1] = useState(0);
  const [x2, setX2] = useState(0);
  const [y2, setY2] = useState(0);
  const [x3, setX3] = useState(0);
  const [y3, setY3] = useState(0);
  const [x4, setX4] = useState(0);
  const [y4, setY4] = useState(0);

  const handleAnimation = useCallback(() => {
    setX0(XCoords[Math.floor(Math.random() * XCoords.length)]);
    setY0(YCoords[Math.floor(Math.random() * YCoords.length)]);
    setX1(XCoords[Math.floor(Math.random() * XCoords.length)]);
    setY1(YCoords[Math.floor(Math.random() * YCoords.length)]);
    setX2(XCoords[Math.floor(Math.random() * XCoords.length)]);
    setY2(YCoords[Math.floor(Math.random() * YCoords.length)]);
    setX3(XCoords[Math.floor(Math.random() * XCoords.length)]);
    setY3(YCoords[Math.floor(Math.random() * YCoords.length)]);
    setX4(XCoords[Math.floor(Math.random() * XCoords.length)]);
    setY4(YCoords[Math.floor(Math.random() * YCoords.length)]);
  }, []);

  useEffect(() => {
    let timer = setInterval(() => {
      requestAnimationFrame(handleAnimation);
    }, fpm / fps);
    return () => {
      clearInterval(timer);
    };
  }, [handleAnimation]);

  return (
    <div className="App">
      <Container>
        {/* {Array.from(Array(2).keys()).map((item, index) => ( */}
        <Animate x={x0} y={y0} />
        <Animate x={x1} y={y1} />
        <Animate x={x2} y={y2} />
        <Animate x={x3} y={y3} />
        <Animate x={x4} y={y4} />
        {/* ))} */}
      </Container>
    </div>
  );
}
